.content {
    margin-top: 50px;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: end;
    width: 95%;
    gap: 20px;
    flex-wrap: wrap;
  }

  @media(max-width: 1200px) {
    .content {
      margin-top:  calc(3px + (50 - 20) * ((100vw - 320px)/(1200 - 320)));
    }
  }