.header {
  margin-top: 35px;
  text-align: center;
}

.header1 {
  font-size: 50px;
}

.small-colored-text {
  color: #789fb0;
  font-style: oblique;
}

@media(max-width: 1200px) {
  .header1 {
    font-size: calc(20px + (50 - 20) * ((100vw - 320px)/(1200 - 320)));
  }

  .header {
    margin-top: calc(10px + (35 - 10) * ((100vw - 320px)/(1200 - 320)));
  }

  .header3 {
    font-size: calc(10px + (18 - 10) * ((100vw - 320px)/(1200 - 320)));
  }
}