.nav {
    width: 80%;
    display: flex;
    justify-content: space-between;
  }

  h1 {
    font-size: 24px;
    margin-top: 10px;
  }
  
  .logo-span {
    color: var(--blue-color);
  }
  
  .socials {
    width: 30%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
  }
  
  .socials-img {
    width: 35px;
    height: 35px;
    border-radius: 50%;
    cursor: pointer;
    &:hover {
      opacity: 0.5;
    }
    transition-duration: 0.7s;
}

@media (max-width: 1200px){
  .socials-img {
    width: calc(20px + (35 - 20) * ((100vw - 320px)/(1200 - 320)));
    height: calc(20px + (35 - 20) * ((100vw - 320px)/(1200 - 320)));
  }

  h1 {
    font-size: calc(15px + (24 - 15) * ((100vw - 320px)/(1200 - 320)));
  }
}