@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100..900&family=Marck+Script&family=Montserrat:ital,wght@0,100..900;1,100..900&family=Nunito:ital,wght@0,200..1000;1,200..1000&family=Oswald:wght@200..700&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&family=Tenor+Sans&display=swap');

:root {
  --blue-color: #02a6ff;
  --light-blue: #95dbf5;
  --def-font-color: #22363f;
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: var(--blue-color);
  font-family: "Nunito", sans-serif;
}

.container {
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-top: 50px;
  margin-bottom: 50px;
  width: 75%;
  height: max-content;
  border-radius: 15px;
  background-color: #f7f9f8;
  box-shadow: 4px 4px 26px 0px rgba(34, 60, 80, 0.37);
  color: var(--def-font-color);
}

@media (max-width: 1200px) {
  .container {
    margin-top: calc(3px + (50 - 20) * ((100vw - 320px)/(1200 - 320)));
    margin-bottom: calc(3px + (50 - 20) * ((100vw - 320px)/(1200 - 320)));
    width: calc(95% + (75 - 3) * ((100vw - 320px)/(1200 - 320)));
  }
}