.app-card {
    position: relative;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
    color: var(--def-font-color);
    text-decoration: none;
    
    width: 30%;
    height: 600px;
    border-radius: 15px;
    background-color: #ffffff;
    box-shadow: 4px 4px 26px 0px rgba(34, 60, 80, 0.1);
    transition-duration: 0.7s;
  
    &:hover {
      box-shadow: 4px 4px 26px 0px rgba(34, 60, 80, 0.7);
    }
  }
  
  .lang-name, .lang-name2 {
    font-size: 12px;
    margin: 5px;
    padding: 5px;
    color: var(--light-blue);
    align-self: self-start;
  }
  
  .lang-name::before {
    content: ' ';
    position:absolute;
    top: 11px;
    left: 70px;
    display: block;
    background-image: url('./svg/js-logo.jpg');
    width: 1em;
    height: 1em;
    background-size:cover;
    opacity: 0.7;
  }
  
  .lang-name2::before {
    content: ' ';
    position:absolute;
    top: 11px;
    left: 58px;
    display: block;
    background-image: url('./svg/react-logo.jpg');
    width: 1em;
    height: 1em;
    background-size:cover;
    opacity: 0.7
  }
  
  .app-card-header {
    font-size: 30px;
    text-align: center;
  }
  
  .card-img, .review-text {
    width: 80%;
  }
  
  .review-text {
    opacity: 0.7;
  }

@media(max-width: 1200px) {
  .app-card {
    height: calc(200px + (600 - 200) * ((100vw - 320px)/(1200 - 320)));
    width: calc(45% + (30 - 45) * ((100vw - 320px)/(1200 - 320)));
  }

  .lang-name, .lang-name2 {
    font-size: calc(9px + (12 - 9) * ((100vw - 320px)/(1200 - 320)));
    margin: calc(0px + (5 - 0) * ((100vw - 320px)/(1200 - 320)));
    padding: calc(0px + (5 - 0) * ((100vw - 320px)/(1200 - 320)));
    margin-left: 10px;
  }

  .lang-name2::before {display: none;}
  .lang-name::before {display: none;}

  .app-card-header {
    font-size: calc(13px + (30 - 13) * ((100vw - 320px)/(1200 - 320)));
    margin: calc(0px + (5 - 0) * ((100vw - 320px)/(1200 - 320)));
  }

  .review-text {
    font-size: calc(7px + (16 - 7) * ((100vw - 320px)/(1200 - 320)));
  }
}